.notFound{
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 10px;
}
.notFound img{
    float: right;
    margin-left: auto;
}
.notFound h1{
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 100px;
    text-align: center;
}



